import { NgModule ,NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { GrnPoMasterRoutingModule } from './grn-po-master-routing.module';
import { GrnPoMasterComponent } from './grn-po-master.component';
import { GrnPoMasterAddComponent } from './grn-po-master-add/grn-po-master-add.component';
import { GrnPoMasterEditComponent } from './grn-po-master-edit/grn-po-master-edit.component';
import { GrnPoItemViewComponent } from './grn-po-item-view/grn-po-item-view.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    GrnPoMasterRoutingModule
  ],
  entryComponents:[
    GrnPoMasterAddComponent,
    GrnPoMasterEditComponent,
    GrnPoItemViewComponent
  ],
  declarations: [
    GrnPoMasterComponent,
    GrnPoMasterAddComponent,
    GrnPoMasterEditComponent,
    GrnPoItemViewComponent
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class GrnPoMasterModule { }



