import { Component, OnInit, Input, Inject, AfterViewInit, ChangeDetectorRef, Output, EventEmitter, ViewChild } from '@angular/core';
import { GrnPoMasterService } from '../../../../shared';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';

@Component({
  selector: 'app-grn-po-item-view',
  templateUrl: './grn-po-item-view.component.html',
  styleUrls: ['./grn-po-item-view.component.scss']
})
export class GrnPoItemViewComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @Input('grnpoitemDetailViewData') grnpoitemDetailViewData;

  apiUrl: string;
  source: any;
  dataAdapter: any;
  columns: any[];
  grn_items: Array<any> = [];

  constructor( @Inject('API_URL') apiUrl: string,
    private cdr: ChangeDetectorRef,
    private gps: GrnPoMasterService) {
    this.apiUrl = apiUrl;
  }

  ngOnInit() {
    this.getItem();
  }
  ngAfterViewInit() {
    this.jqxLoader.open();
    this.gps.show(this.grnpoitemDetailViewData.id).subscribe(
      result => {
        this.grn_items = result['grn_items'];
        for (var _i = 0; _i < this.grn_items.length; _i++) {
          this.grn_items[_i]['name'] = this.grn_items[_i]['item'] && this.grn_items[_i]['item'].name + '-'+ this.grn_items[_i]['item'].code;
          this.grn_items[_i]['uname'] = this.grn_items[_i]['item'] && this.grn_items[_i]['item']['unit'] && this.grn_items[_i]['item']['unit'].name;
          this.grn_items[_i]['unit_id'] = this.grn_items[_i]['unit'] && this.grn_items[_i]['item'].unit_id;
          this.grn_items[_i]['po_no'] = this.grn_items[_i]['purchase_order'] && this.grn_items[_i]['purchase_order'].po_no;
        }
        this.source.localdata = this.grn_items;
        this.myGrid.updatebounddata("cells");
        this.jqxLoader.close();
        console.log(this.grn_items);
      },
      error => {
        console.log(error);
      }
    );
    this.cdr.detectChanges();
  }

  getItem() {
    this.source =
      {
        localdata: {},
        datatype: 'json',
        datafields:

        [
          { name: 'po_id', type: 'string' },
          { name: 'po_no', type: 'string' },
          { name: 'item_id', type: 'string' },
          { name: 'name', type: 'string' },
          { name: 'uname', type: 'string' },
          { name: 'quantity', type: 'string' },
          { name: 'rate', type: 'string' },
          { name: 'unit_id', type: 'string' },
          { name: 'value', type: 'string' },
          { name: 'vat', type: 'string' },
          { name: 'total', type: 'string' },
        ],
        updaterow: (rowid: any, rowdata: any, commit: any): void => {
          commit(true);
        }
      };

    this.dataAdapter = new jqx.dataAdapter(this.source);

    this.columns =
      [
        {
          text: 'Po Req No', datafield: 'po_id', displayfield: 'po_no', width: 120, editable:false,
          // createeditor: (row: number, column: any, editor: any): void => {
          //   let contractorsource =
          //     {
          //       datatype: "json",
          //       contentType: "application/json; charset=utf-8",
          //       datafields: [
          //         { name: 'id' },
          //         { name: 'po_no' }
          //       ],
          //       url: this.apiUrl + 'po_requisition_detail?limit=all'
          //     };
          //   let contractorAdapter = new jqx.dataAdapter(contractorsource, {
          //     downloadComplete: (data, status, xhr) => {
          //       let tData = data.data;
          //       return tData;
          //     }
          //   });
          //   editor.jqxComboBox({
          //     source: contractorAdapter,
          //     displayMember: "po_no",
          //     valueMember: "id",
          //   });
          // }
        },
        {
          text: 'Item Name', datafield: 'item_id', displayfield: 'name', width: 120, editable:false,
          // createeditor: (row: number, column: any, editor: any): void => {
          //   let contractorsource =
          //     {
          //       datatype: "json",
          //       contentType: "application/json; charset=utf-8",
          //       datafields: [
          //         { name: 'id' },
          //         { name: 'name' },
          //       ],
          //       async: false,
          //       url: this.apiUrl + 'item?limit=all'
          //     };
          //   let contractorAdapter = new jqx.dataAdapter(contractorsource, {
          //     downloadComplete: (data, status, xhr) => {
          //       let tData = data.data;
          //       return tData;
          //     }
          //   });
          //   editor.jqxComboBox({
          //     source: contractorAdapter,
          //     displayMember: "name",
          //     valueMember: "id"
          //   });
          // }
        },
        { text: 'Quantity', datafield: 'quantity', columntype: 'textbox',editable:false },
        { text: 'Rate', datafield: 'rate', columntype: 'textbox', editable:false, },
        {
          text: 'Unit Name', datafield: 'unit_id', displayfield: 'uname', columntype: 'combobox', width: 120, editable:false,
          // createeditor: (row: number, column: any, editor: any): void => {
          //   let contractorsource =
          //     {
          //       datatype: "json",
          //       contentType: "application/json; charset=utf-8",
          //       datafields: [
          //         { name: 'id' },
          //         { name: 'name' },
          //       ],
          //       async: false,
          //       url: this.apiUrl + 'unit?limit=all'
          //     };
          //   let contractorAdapter = new jqx.dataAdapter(contractorsource, {
          //     downloadComplete: (data, status, xhr) => {
          //       let tData = data.data;
          //       return tData;
          //     }
          //   });
          //   editor.jqxComboBox({
          //     source: contractorAdapter,
          //     displayMember: "name",
          //     valueMember: "id"
          //   });
          // }
        },
        { text: 'Value', datafield: 'value', columntype: 'textbox', editable:false },
        { text: 'Vat', datafield: 'vat', columntype: 'textbox', editable:false },
        {
          text: 'Total', datafield: 'total', columntype: 'textbox', editable:false,
          cellsrenderer: (index: number, datafield: string, value: any, defaultvalue: any, column: any, rowdata: any): string => {
            if (rowdata.quantity && rowdata.rate) {
              let total = parseFloat(rowdata.quantity) * parseFloat(rowdata.rate);
              return '<div style="jqx-align-right">' + this.dataAdapter.formatNumber(total, 'f2') + '</div>';
            }
          }
        },
        // {
        //   text: 'Actions', datafield: 'Delete', columntype: 'button', width: 70,
        //   cellsrenderer: (): string => {
        //     return 'Delete';
        //   },
        //   buttonclick: (row: number): void => {
        //     let rowid = this.myGrid.getrowid(row)
        //     this.myGrid.deleterow(rowid);
        //   }
        // }
      ];

  }
  rendertoolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';

    let headingContainer4 = document.createElement('div');

    headingContainer4.id = 'headingContainer4';

    headingContainer4.style.cssText = 'float: left;padding:5px;margin-right:10px;font-weight:bold;';
  
    headingContainer4.innerHTML = 'Grn Po Items:';

    container.appendChild(headingContainer4);
    toolbar[0].appendChild(container);

  
  };

}



