import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { GrnPoMasterComponent } from './grn-po-master.component';

const routes: Routes = [
    {
        path: '', component: GrnPoMasterComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class GrnPoMasterRoutingModule { }



